  height: 100%;
}
body {
  height: 100%;
  margin: 0;

  display: flex;
  flex-direction: column;
}
* {
  box-sizing: border-box;
  font-family: "Cabin", sans-serif;
}
header {
  width: 100%;
  height: 44px;
  background: #7b2cbf;

  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px 0 20px;
}
button {
  width: 20px;
  height: auto;
  padding: 0;
  background-color: transparent;
  border: none;
}
.part1,
.part2 {
  display: flex;
  gap: 15px;
}
.profile {
  width: 25px;
  height: 25px;
  border-radius: 50px;
  background: url("https://avatars.githubusercontent.com/u/98334884?v=4");
  background-size: contain;
}
header > h1 {
  color: white;
  margin: 0;
}

.sidebar {
  width: 250px;
  flex: 1;
  background-color: rgba(128, 128, 128, 0.103);

  display: flex;
  flex-direction: column;
  transition: 0.6s;

  height: 100%;
  position: fixed;
  z-index: 1;
  top: 20;
  left: 20;
  padding-top: 20px;
  overflow-x: hidden;
}
.sidebar.inactive {
  width: 0px;
}
.sidebar-btn {
  transition: 0.6s;
  fill: white;
}
.sidebar-btn.inactive {
  transform: rotate(90deg);
}
.sidebar > div {
  padding-left: 16px;
  padding-right: 16px;
}
.due {
  flex: 1;

  display: flex;
  flex-direction: column;
  padding-top: 30px;
}
.projects {
  flex: 8;
}
.sidebar > div > button {
  font-size: 14px;
  width: 220px;
  border-radius: 4px;
  padding: 7px;

  display: flex;
  gap: 8px;
  align-items: center;
}
.sidebar > div > button:hover {
  background-color: rgba(128, 128, 128, 0.116);
  cursor: pointer;
}
.today-icon,
.upcoming-icon {
  width: 17px;
}
.today-icon {
  filter: invert(36%) sepia(14%) saturate(4743%) hue-rotate(130deg)
    brightness(91%) contrast(101%);
}
.upcoming-icon {
  filter: invert(65%) sepia(63%) saturate(6667%) hue-rotate(202deg)
    brightness(100%) contrast(102%);
}
.projects > h4 {
  font-size: 15px;
  margin-top: 0;
  margin-bottom: 17px;
  padding: 7px;
  padding-left: 0;

  display: flex;
  justify-content: space-between;
  border-bottom: black solid 1px;
}
.add-project {
  justify-content: center;
}
.add-project > img {
  width: 17px;
}
.add-project:hover {
  cursor: pointer;
}
label {
  display: flex;
  flex-direction: column;
}
input {
  border-radius: 4px;
  border: solid rgba(128, 128, 128, 0.555) 1px;
  padding: 4px;
  transition: 0.3s;
}
input:focus {
  outline: none;
  border: solid black 1px;
}
.popup {
  position: fixed;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
  transition: opacity 300ms ease-in-out;
  border-radius: 7px;
  height: 180px;
  z-index: 10;
  min-width: 25%;
  padding: 0 15px 0 15px;
  background: white;

  display: flex;
  flex-direction: column;
}
.overlay {
  position: fixed;
  opacity: 0;
  transition: 200ms ease-in-out;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
}
.popup.active {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}
.overlay.active {
  opacity: 1;
  pointer-events: all;
}
.popup form {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.buttons {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-top: 30px;
}
.buttons > button {
  width: 65px;
  padding: 7px;
  border-radius: 4px;
}
.buttons > .add {
  background-color: #7b2cbf;
  color: white;
}
.buttons > .cancel {
  background-color: rgba(128, 128, 128, 0.116);
}
button:hover {
  cursor: pointer;
}
.projects-container {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.project {
  padding: 7px;
  border-radius: 4px;
  font-size: 14px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.project:hover {
  background-color: rgba(128, 128, 128, 0.116);
}
.todo-popup,
.edit-popup {
  position: fixed;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
  transition: opacity 300ms ease-in-out;
  border-radius: 7px;
  height: 180px;
  z-index: 10;
  min-width: 30%;
  padding: 5px 15px 5px 15px;
  background: white;

  display: flex;
  flex-direction: column;

  border: solid black 1px;
}
.todo-popup.active,
.edit-popup.active {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}
.todo-popup form,
.edit-popup form {
  display: flex;
  flex-direction: column;

  height: 100%;
  width: 100%;
}
.todo-popup #todo-title,
.edit-popup #edit-title {
  font-size: 20px;
  border: none;
}
.todo-popup #desc,
.edit-popup #edit-desc {
  border: none;
}
.todo-popup .buttons,
.edit-popup .buttons {
  justify-content: space-between;
}
.leftBtns,
.rightBtns {
  display: flex;
  gap: 5px;
  justify-content: flex-end;
}
.leftBtns {
  justify-content: start;
}
.buttons div button {
  width: 65px;
  padding: 7px;
  border-radius: 4px;
}
.rightBtns .add {
  background-color: #7b2cbf;
  color: white;
}
.rightBtns button,
.leftBtns button {
  height: 30px;
  background-color: rgba(128, 128, 128, 0.116);
}
.rightBtns .edit-confirm {
  background-color: #7b2cbf;
  color: white;
}
.leftBtns .date {
  width: 70px;
}
#date,
#edit-date {
  background-color: rgba(128, 128, 128, 0.116);
  border: none;
  width: 108px;
}
.dropdowns,
.edit-dropdowns {
  display: flex;
  padding: 10px 0 10px 0;
}
.priority,
#projectSelect,
.edit-priority,
#edit-projectSelect {
  border: #7b2cbf 1px solid;
  border-radius: 4px;
  background: none;
}
.priority:focus,
#projectSelect:focus,
.edit-priority:focus,
#edit-projectSelect:focus {
  outline: none;
}
#projectSelect,
#edit-projectSelect {
  padding: 4px;
}
.content-wrapper {
  display: flex;
  height: 100vh;
}
.content {
  flex: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-left: 250px;
  transition: 0.6s;
}
.todo {
  border-bottom: solid rgba(189, 189, 189, 0.555) 1px;
  width: 60%;
  height: 60px;
  display: flex;
  flex-direction: row-reverse;
}
.title-box {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 10px;
}
.other-info {
  flex: 1;
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
}
.title-wrapper > p {
  margin: 0;
}
.desc {
  font-size: 12px;
}
.edit {
  width: 18px;
  height: 18px;
}
.edit:hover {
  cursor: pointer;
}
.trash {
  width: 18px;
  height: 18px;
}
.trash:hover {
  cursor: pointer;
}
.todo-date,
.todo-priority {
  font-size: 15px;
}
.content > div {
  display: none;
}
.Default.active {
  width: 100%;
  flex: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}
.content > div.active {
  width: 100%;
  flex: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  padding: 20px;
}
.remove {
  height: 14px;
  width: 14px;
  background: center / contain url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
.checkbox {
  accent-color: #7b2cbf;
}
